import * as React from 'react'
import { MAPBOX_TOKEN } from '../../helper/constants';
import KeplerGl from 'kepler.gl';
import './map.scss'
import JsonData from './json/covid.json'
import ConfigJson from './json/config.json'
import { addDataToMap } from "kepler.gl/actions";
import { connect } from 'react-redux';

class KeplerGlView extends React.Component <any, any> {
  constructor(props: any) {
    super(props)
  }

  componentDidMount() {
    this.fetchData()
  }

  private fetchData = async () => {
    const savedDatasets = await JsonData
    if (savedDatasets) {
      const config = await ConfigJson;
      this.props.dispatch(
        addDataToMap({
          datasets: {
            info: {
              label: "Transport",
              id: "transport"
            },
            data: savedDatasets
          },
          option: {
            centerMap: true,
            readOnly: false,

          },
          config: config
        })
      );
    }
  }

  render() {
    return (
        <KeplerGl
         id="keplerGl"
         width={'70vw'}
         mapboxApiAccessToken={MAPBOX_TOKEN}
         height={'100vh'}/>
    );
  }
}
const mapStateToProps = (state: any) => state;
const dispatchToProps = (dispatch: any) => ({dispatch});


export default connect(mapStateToProps, dispatchToProps)(KeplerGlView)
