import React from 'react';
import './App.scss';
import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Menu from './component/menu';
import Sidebar from './component/sidebar';
import Driver from './component/drivers';
import Warehouse from './component/warehouse';
import Salary from './component/salary';
import Role from './component/role';
import LiveMapUpdate from './component/liveMapUpdate';
import DrawLine from './component/drawLine';

class App extends React.Component {
  render() {
    return (
      <Router>
      <div className="transport-app">
        <Menu/>
        <Sidebar/>
        <Switch>
          <Route exact path="/">
            <Driver/>
          </Route>
          <Route exact path="/driver">
            <Driver />
          </Route>
          <Route exact path="/warehouse">
            <Warehouse/>
          </Route>
          <Route exact path="/salary">
            <Salary/>
          </Route>
          <Route exact path="/draw-line">
            <DrawLine/>
          </Route>
          <Route exact path="/role">
            <Role/>
          </Route>
          <Route exact path="/live-update">
            <LiveMapUpdate/>
          </Route>
        </Switch>

      </div>
      </Router>
    );
  }
}

export default App;
