import React from 'react';
import './drivers.scss';
import DeckGLViewLiner from './map/DeckGLViewLiner';

class Role extends React.Component {
  render() {
    return (
      <div className="driver-section" id="driver-section">
        <div className="driver-container">
          <div className="row mb-3 cdbd-input-row">
            <div className="row-wrap">
              <p className="driver-input-text">Choose a driver enter CDBD</p>
            </div>
            <div>
              <input type="text" id="cdbdNumber" name="cdbdNumber" className="driver-cdbd-input" placeholder="123456" />
            </div>
          </div>
          <DeckGLViewLiner/>
          <div className="row map-destination-wrap pb4">
            <div className="row-wrap">
              <p className="router-map-text">
                <i className="fa fa-map pr-2 router-map-icon" aria-hidden="true"></i>
                Daily Router map
              </p>
            </div>
            <div className="row-wrap">
              <button className="button">
                Next Destination
                <i className="fa fa-chevron-right pl-2" aria-hidden="true"></i>
              </button>
            </div>
          </div>
          <div className="row input-areas">
            <div className="col">
              <div className="row-wrap">
                <p className="expected-actual-text">Expected Travel Distance:</p>
                <input type="text" id="cdbdNumber" className="route-imput" placeholder="12" />
                <span className="ml-1">KM</span>
              </div>
            </div>
            <div className="col">
              <div className="row-wrap">
                <p className="expected-actual-text">Expected Travel Distance:</p>
                <input type="text" id="cdbdNumber" className="route-imput" placeholder="12" />
                <span className="ml-1">KM</span>
              </div>
            </div>
          </div>

          <div className="row input-areas">
            <div className="col">
              <div className="row-wrap">
                <p className="expected-actual-text">Expected Travel Time:</p>
                <input type="text" id="cdbdNumber" className="route-imput" placeholder="12" />
                <span className="ml-1">KM</span>
              </div>
            </div>
            <div className="col">
              <div className="row-wrap">
                <p className="expected-actual-text">Actual Travel Distance:</p>
                <input type="text" id="cdbdNumber" className="route-imput" placeholder="12" />
                <span className="ml-1">Mins</span>
              </div>
            </div>

          </div>
        </div>
      </div>
    );
  }
}

export default Role;
