import * as React from 'react'
import ReactMapGL, {  NavigationControl } from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import DeckGL from '@deck.gl/react';
import { GeoJsonLayer, ArcLayer } from 'deck.gl';
import JsonData from './json/data.json'
import GL from '@luma.gl/constants';
import { Trip } from '../../types/transport';


export default class DeckGLView extends React.Component <any, any>{
  constructor(props: any) {
    super(props);
    this.state = {
      viewport: INITIAL_VIEW_SET
    }
  }

  componentDidMount() {
    INITIAL_VIEW_SET.zoom = 10
    this.setState({viewport: INITIAL_VIEW_SET})
  }

  render() {
    const tripData: Trip[] = JsonData

    const layers = [
      new GeoJsonLayer({
        id:                   'transport',
        data:                 tripData,
        filled:               true,
        pointRadiusMinPixels: 1,
        pointRadiusScale:     2000,
        getRadius:            .2,
        getFillColor:         [55, 114, 223],
        pickable:             true,
        autoHighlight:        true
      }),
      new ArcLayer({
        id:                'arcs',
        data:              tripData,
        // @ts-ignore
        dataTransform:     (trip: any) => trip.filter((trip: Trip) => trip.properties.scalerank < 4),
        getSourcePosition: () => [90.3442, 23.7837],
        getTargetPosition: (d: any) => d.geometry.coordinates,
        getSourceColor:    [241, 96, 73],
        getTargetColor:    [55, 114, 223],
        getWidth:          1
      })
  ];
    return (
      <ReactMapGL
       {...this.state.viewport}
       width="75vw"
       height="50vh"
       parameters={{
        blendFunc: [GL.SRC_ALPHA, GL.ONE, GL.ONE_MINUS_DST_ALPHA, GL.ONE],
        blendEquation: GL.FUNC_ADD
        }}
       onViewportChange={viewport => this.setState({viewport})}
       mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
       mapboxApiAccessToken={MAPBOX_TOKEN}>
        <DeckGL
         initialViewState={INITIAL_VIEW_SET}
         controller={true}
         pickingRadius={5}
         layers={layers}>
        </DeckGL>
        <div style={{ position: 'absolute', bottom:20, right: 0 }}>
          <NavigationControl />
        </div>
      </ReactMapGL>
    );
  }
}
