import * as React from 'react'
import ReactMapGL, {  NavigationControl } from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import DeckGL from '@deck.gl/react';
import { LineLayer } from 'deck.gl';
import GL from '@luma.gl/constants';
import { ScatterplotLayer } from 'deck.gl';


export default class DeckGLViewLiner extends React.Component <any, any>{
  constructor(props: any) {
    super(props);
    this.state = {
      viewport: INITIAL_VIEW_SET
    }
  }

  componentDidMount() {
    INITIAL_VIEW_SET.zoom = 1
    this.setState({viewport: INITIAL_VIEW_SET})
  }

  render() {
    // Now we are getting sample Json from outside becuase we need huge latitute and longitute but we will make a own JSON based on common
    const AirportJson = 'https://raw.githubusercontent.com/visgl/deck.gl-data/master/examples/line/airports.json';
    const FlightJson  = 'https://raw.githubusercontent.com/visgl/deck.gl-data/master/examples/line/heathrow-flights.json';

    const layers = [
        new ScatterplotLayer({
        id: 'scatter',
        data: AirportJson,
        radiusScale: 20,
        getPosition: (d: any) => d.coordinates,
        getFillColor: [255, 140, 0],
        getRadius: (d: any) => this.getSize(d.type),
        pickable: true
      }),
      new LineLayer({
        id: 'line-layer',
        data: FlightJson,
        pickable: true,
        opacity: 1,
        getSourcePosition: (d: any) => d.start,
        getTargetPosition: (d: any) => d.end,
        getColor: (d: any) => this.getColor(d),
        getWidth: 3
      })
  ];
    return (
      <ReactMapGL
       {...this.state.viewport}
       width="75vw"
       height="50vh"
       parameters={{
        blendFunc: [GL.SRC_ALPHA, GL.ONE, GL.ONE_MINUS_DST_ALPHA, GL.ONE],
        blendEquation: GL.FUNC_ADD
        }}
       onViewportChange={viewport => this.setState({viewport})}
       mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
       mapboxApiAccessToken={MAPBOX_TOKEN}>
        <DeckGL
         initialViewState={INITIAL_VIEW_SET}
         controller={true}
         pickingRadius={5}
         layers={layers}>
        </DeckGL>
        <div style={{ position: 'absolute', bottom:20, right: 0 }}>
          <NavigationControl />
        </div>
      </ReactMapGL>
    );
  }

  private getColor = (d: any) : any => {
    const z = d.start[2];
    const r = z / 10000;
    return [255 * (1 - r * 2), 128 * r, 255 * r, 255 * (1 - r)];
  }

  private getSize = (type: any) : number =>  {
    if (type.search('major') >= 0) {
      return 100;
    }
    if (type.search('small') >= 0) {
      return 30;
    }
    return 60;
  }
}
