import React from 'react';
import { NavLink } from 'react-router-dom';
import './sidebar.scss';

class Sidebar extends React.Component {
  render() {

    return (
      <div className="transport-sidebar" id="transport-sidebar">
        <div className="sidebar-container">
          <div className="app-big-text">
            <h2 className="transport-text">Transport</h2>
            <div className="sidebar-menu">
              <ul>
                <li>
                  <NavLink
                      to={'/driver'} activeClassName="active-sidebar">
                      <i id="sidebar-icon" className="fa fa-bicycle" aria-hidden="true"></i>
                      Driver
                  </NavLink >
                </li>
                <li>
                  <NavLink to="/warehouse" activeClassName="active-sidebar">
                    <i id="sidebar-icon" className="fa fa-fire" aria-hidden="true"></i>
                    Warehouse
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/salary" activeClassName="active-sidebar">
                    <i id="sidebar-icon" className="fa fa-fire" aria-hidden="true"></i>
                    Salary
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/draw-line" activeClassName="active-sidebar">
                    <i id="sidebar-icon" className="fa fa-fire" aria-hidden="true"></i>
                    Draw Line
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/role" activeClassName="active-sidebar">
                    <i id="sidebar-icon" className="fa fa-fire" aria-hidden="true"></i>
                    Role
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/live-update" activeClassName="active-sidebar">
                    <i id="sidebar-icon" className="fa fa-fire" aria-hidden="true"></i>
                    Live update
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;
