import * as React from 'react'
import ReactMapGL, { Marker, NavigationControl, Source, Layer} from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import CoordinateJson from './json/data.json'
import CoordinateLineJson from './json/line.json'
import MarkerImage from '../../assets/image/svg/marker.svg'
import { Path, Trip } from '../../types/transport';


export default class ReactMapGlWithDrawLine extends React.Component <any, any>{
    constructor(props: any) {
      super(props)
      this.state = {
        viewport: INITIAL_VIEW_SET
      }
    }

    componentDidMount() {
      INITIAL_VIEW_SET.zoom = 10
      this.setState({viewport: INITIAL_VIEW_SET})
    }

    render () {
        const COORDINATES: Trip[] = CoordinateJson
        // TODO: need to implement proper type here.
        const PathData: any = {
          type: "Feature",
          properties: {},
          geometry: {
            type: "LineString",
            coordinates: CoordinateLineJson
          }
        }
        return (
          <ReactMapGL
           {...this.state.viewport}
           width="75vw"
           height="50vh"
           onViewportChange={viewport => this.setState({viewport})}
           // mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
           mapboxApiAccessToken={MAPBOX_TOKEN}>
              <Marker
               key={COORDINATES[0].id}
               latitude={COORDINATES[0].geometry.coordinates[1]}
               longitude={COORDINATES[0].geometry.coordinates[0]}
               offsetLeft={-20}
               offsetTop={-10}>
                <img src={MarkerImage} style={{border: 'none'}}/>
              </Marker>
              <Marker
               key={COORDINATES[1].id}
               latitude={COORDINATES[1].geometry.coordinates[1]}
               longitude={COORDINATES[1].geometry.coordinates[0]}
               offsetLeft={-20}
               offsetTop={-10}>
                <img src={MarkerImage} style={{border: 'none'}}/>
              </Marker>
              <Source  id='route' type='geojson' data={PathData} >
                <Layer
                 id='linelayer1'
                 type='line'
                 source='route'
                 layout={{
                    'line-join': 'round',
                    'line-cap': 'round'
                 }}
                 paint={{
                    'line-color': '#888',
                    'line-width': 8
                 }}/>
                </Source >
              <div style={{ position: 'absolute', bottom:20, right: 0 }}>
                <NavigationControl />
              </div>
            </ReactMapGL>
        )
    }
}