import * as React from 'react'
import ReactMapGL, { Marker, NavigationControl } from 'react-map-gl';
import { INITIAL_VIEW_SET, MAPBOX_TOKEN } from '../../helper/constants';
import MarkerImage from '../../assets/image/svg/marker.svg'
import io from "socket.io-client/dist/socket.io.js";

export default class ReactMapGlView extends React.Component <any, any>{

    constructor(props: any) {
      super(props)
      this.state = {
        location: null,
        viewport: INITIAL_VIEW_SET
      }
    }
    componentDidMount() {
      INITIAL_VIEW_SET.zoom = 12
      this.setState({viewport: INITIAL_VIEW_SET})

      // Note: we need to run node server for socket.io just use command in new terminal (node server)
      let socket = this.makeSocketConnection()
      this.listenAndUpdateLocation(socket)
    }

    private listenAndUpdateLocation = (socket: any) => [
        socket.on('location', (location: Location) => {
            this.setState({location})
          })
    ]

    private makeSocketConnection = () => {
      // TODO: need to set server url
      const socket = io.connect('http://localhost:8000', {
          transports: ['websocket', 'polling', 'flashsocket']})
      return socket
    }

    render () {
        const locationInfo = this.state.location
        return (
          <ReactMapGL
           {...this.state.viewport}
           width="75vw"
           height="50vh"
           onViewportChange={viewport => this.setState({viewport})}
            // mapStyle='mapbox://styles/razbiswas/ckj10rq0l1g4k1aruildzqtqb'
           mapboxApiAccessToken={MAPBOX_TOKEN}>
              {
                locationInfo && locationInfo.coords ?
                <Marker
                 key={locationInfo.uuid}
                 latitude={locationInfo.coords.latitude}
                 longitude={locationInfo.coords.longitude}>
                  <img src={MarkerImage} style={{ border: 'none' }} />
                </Marker> : null
              }
              <div style={{ position: 'absolute', bottom:20, right: 0 }}>
                <NavigationControl />
              </div>
            </ReactMapGL>
        )
    }
}