import React from 'react';
import './menu.scss';

type onClick = React.MouseEvent<HTMLElement>

interface menuActionProps {
  toggleMenu?: (event: onClick) => void;
}

interface menuBarState {
  isSidebarOpen: Boolean;
}

class Menu extends React.Component<menuActionProps, menuBarState> {

  constructor(props: menuActionProps) {
    super(props)
    this.state = {
      isSidebarOpen: true
    }
  }


  render() {
    return (
      <div className="transport-menu">
        <header className="menu-header">
          <div onClick={(e) => this.toggleMenu(e)} className="left-item" role="button">
            <i className="fa fa-bars" aria-hidden="true"></i>
          </div>
          <div className="center-item">
            <h2 className="title">
              <span className="highlightedTitle">Transport </span>
              Admin Panel
            </h2>
          </div>
          <div className="right-item">
            <div className="dropdown show">
              <a
               href="#"
               role="button"
               id="dropdownMenuLink"
               data-toggle="dropdown"
               aria-haspopup="true"
               aria-expanded="false">
               <i className="fa fa-user user-icon" aria-hidden="true"></i>
              </a>
              <div className="dropdown-menu" aria-labelledby="dropdownMenuLink">
                <a className="dropdown-item" href="#">
                  <i className="fa fa-cog" aria-hidden="true"></i>
                  Setting
                  </a>
                <a className="dropdown-item" href="#">
                  <i className="fa fa-list" aria-hidden="true"></i>
                  Other
                </a>
                <a className="dropdown-item" href="#">
                  <i className="fa fa-arrow-right" aria-hidden="true"></i>
                  Logout
                </a>
              </div>
            </div>
          </div>
        </header>
      </div>
    );
  }

  toggleMenu = (event: onClick) => {
    const { isSidebarOpen } = this.state;
    let sliderElement: any = document.getElementById('transport-sidebar');
    let driverElement: any = document.getElementById('driver-section');
    let windowWidth: number = window.innerWidth;

    if (isSidebarOpen && windowWidth > 672) {
      sliderElement.style.width = 0;
      sliderElement.style.display = 'none';
      driverElement.style.marginLeft = '20px';
      this.setState({ isSidebarOpen: !isSidebarOpen });
    } else {
      sliderElement.style.removeProperty("width");
      sliderElement.style.removeProperty("display");
      driverElement.style.marginLeft = '260px';
      this.setState({ isSidebarOpen: !isSidebarOpen });
    }
    if (isSidebarOpen && windowWidth < 672) {
      sliderElement.style.width = "100%";
      sliderElement.style.display = 'block'
      driverElement.style.marginLeft = '20px';
      this.setState({ isSidebarOpen: !isSidebarOpen });
    } else {
      sliderElement.style.width = 0;
      sliderElement.style.display = 'none';
      driverElement.style.marginLeft = '20px';
      this.setState({ isSidebarOpen: !isSidebarOpen });
    }
    if (!isSidebarOpen && windowWidth > 672) {
      sliderElement.style.removeProperty("width");
      sliderElement.style.removeProperty("display");
      driverElement.style.marginLeft = '260px';
      this.setState({ isSidebarOpen: !isSidebarOpen });
    }

  }
}

export default Menu;
